<template>
    <div class="row">
        <app-stock
            v-for="(stock, index) in stocks"
            :key="index"
            :stock="stock"
        />
    </div>
</template>

<script>
    import Stock from './Stock';

    export default {
        name: "Stocks",
        components: {
            appStock: Stock
        },
        computed: {
            stocks() {
                return this.$store.getters.stocks;
            }
        }
    }
</script>

