<template>
    <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
        <router-link class="navbar-brand" to="/">Stock trader</router-link>
        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span class="navbar-toggler-icon"></span>
        </button>

        <div class="collapse navbar-collapse" id="navbarSupportedContent">
            <ul class="navbar-nav mr-auto">
                <router-link tag="li" class="nav-item" active-class="active" to="/portfolio"><a class="nav-link">portfolio</a></router-link>
                <router-link tag="li" class="nav-item" active-class="active" to="/stocks"><a class="nav-link">Stocks</a></router-link>
            </ul>

            <div class="navbar-nav ml-auto">
                <ul class="navbar-nav">
                    <li class="nav-item"><a href="#" class="nav-link" @click.prevent="randomizeStocks">End Day</a></li>
                    <li class="nav-item dropdown">
                        <a class="nav-link dropdown-toggle mr-4" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            Save & Load
                        </a>
                        <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                            <a class="dropdown-item" href="#" @click="saveData">Save</a>
                            <a class="dropdown-item" href="#" @click="loadData">Load</a>
                        </div>
                    </li>
                </ul>
                <strong class="navbar-text">Funds: {{$store.getters.funds | currency}}</strong>
            </div>
        </div>
    </nav>
</template>

<script>
    import {mapActions} from 'vuex';

    export default {
        name: "Header",
        methods: {
            ...mapActions({
                randomizeStocks: 'randomizeStocks',
                fetchData: 'loadData'
            }),
            saveData() {
                const data = {
                    funds: this.$store.getters.funds,
                    stockPortfolio: this.$store.getters.stockPortfolio,
                    stocks: this.$store.getters.stocks
                };

                this.$http.put('data.json', data);
            },
            loadData() {
                this.fetchData();
            }
        },
    }
</script>

