import Vue from 'vue';

const loadData = ({commit}) => {
    Vue.http.get('data.json')
        .then(res => {
            const stocks = res.data.stocks;
            const stockPortfolio = res.data.stockPortfolio;
            const funds = res.data.funds;

            commit('setStocks', stocks);
            commit('setPortfolio', {
                stockPortfolio,
                funds
            });
        })
        .catch(err => {
            console.log(err)
        })
};

export {
    loadData
}